export function translate(atr){
  const dictioary={
    author:"",
    date_utc:"",
    date_register:"",
    date_modify:"",
    title:"",
    category:"",
    status:"",
    excerpt:"",
    comment_status:"",
    comment_count:"",
    filter_key:"",
    role:"",
    name:"",
    password:"",
    nickname:"",
    email:"",
    count:"",
    note:""
  };
  return dictioary[atr]!==undefined?dictioary[atr]:atr;
}


