import {createStore,combineReducers,applyMiddleware,compose} from 'redux';
import thunkMiddleware from 'redux-thunk';
import Reducer  from './reducer';

/**trunkMiddlewareaction(dispatch,getState,...)
*
*function createThunkMiddleware(extraArgument) {
  return ({ dispatch, getState }) => next => action => {
    if (typeof action === 'function') {
      return action(dispatch, getState, extraArgument);
    }

    return next(action);
  };
}
*/
const middlewares=[thunkMiddleware];
/**combinReducers
*HomeStatereducerstate
*/
const reducer=combineReducers({
  App:Reducer
})
/** applyMiddleware()next({ getState, dispatch }) => next => action
*compose(),storeEnhancer
*middeware,
*/
const storeEnhancers=compose(applyMiddleware(...middlewares));


//createStorestoreEnhancer
export default createStore(reducer,window.initState||{},storeEnhancers);


